import React, { Component } from 'react';
import { Button } from 'element-react';
import './index.scss';
import { fromJS, is } from 'immutable';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';

class XButton extends Component {
	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		const { defaultOptions } = this.props.configs
		return (
			<div id="x-button">
				<h3>Button 按钮</h3>
				<div className="mt-20 mb-20">
					<Button>默认按钮</Button>
					<Button type="primary">主要按钮</Button>
					<Button type="text">文字按钮</Button>
				</div>
				<Codemirror value={
					`<Button>默认按钮</Button>
<Button type="primary">主要按钮</Button>
<Button type="text">文字按钮</Button>`
				} options={defaultOptions} />

				<div className="mt-20 mb-20">
					<Button plain={true} disabled={true}>默认按钮</Button>
					<Button type="primary" disabled={true}>主要按钮</Button>
					<Button type="text" disabled={true}>文字按钮</Button>
				</div>
				<Codemirror value={
					`<Button plain={true} disabled={true}>默认按钮</Button>
<Button type="primary" disabled={true}>主要按钮</Button>
<Button type="text" disabled={true}>文字按钮</Button>`
				} options={defaultOptions} />

				<div className="mt-20 mb-20">
					<Button type="success">成功按钮</Button>
					<Button type="warning">警告按钮</Button>
					<Button type="danger">危险按钮</Button>
					<Button type="info">信息按钮</Button>
				</div>
				<Codemirror value={
					`<Button type="success">成功按钮</Button>
<Button type="warning">警告按钮</Button>
<Button type="danger">危险按钮</Button>
<Button type="info">信息按钮</Button>`
				} options={defaultOptions} />
			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(XButton)
